@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap);
body {
  background-image: url(/hockeypista/static/media/sfondo.fbeadff2.jpg);
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

*,
*:active {
  outline: 0 !important;
}

.link-unstyled,
.link-unstyled:link,
.link-unstyled:hover {
  color: inherit;
  text-decoration: inherit;
}

.link-black,
.link-black:link,
.link-black:hover {
  color: inherit;
}

.link-black:hover,
.link-black:focus {
  text-decoration: underline;
}

.container-fluid,
.container {
  padding: 5px;
}

.row {
  margin: 0;
}

.col {
  padding: 0;
}

.highlight {
  cursor: pointer;
}

.highlight:hover,
.highlight:focus {
  background-color: #eee;
}

.card {
  margin: 5px;
}

.card-body {
  padding: 1rem;
}

h5.card-title {
  margin: 0;
}

.link-title {
  padding: 0 5px;
}

.scrollbox {
  overflow: auto;
  width: 100%;
  max-height: 35vh;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  border-radius: 0.75rem;
}

.scrollbox:hover {
  border-color: #ddd;
}

::-webkit-scrollbar {
  width: 0px;
}

/* CLASSIFICA SQUADRA ELEMENTO */
.cls-card {
  padding: 0.5em;
}

.cls-col1{
  width: 15%;
  text-align: center;
}

.cls-col3 {
  width: 10%;
  text-align: center;
}

.cls-col2 {
  width: 60%;
  text-align: left;
}

.cls-col4 {
  width: 15%;
  text-align: center;
}

.cal-elem-outer {
  margin: 5px;
}

.cal-elem-inner {
  padding: 3px !important;
}

.highlight {
  cursor: pointer;
}

.highlight:hover,
.highlight:focus {
  background-color: #eee;
}

.cal-elem-row,
.cal-flex {
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
}

.cal-flex-rev {
  display: flex !important;
  flex-direction: row-reverse !important;
  align-items: center !important;
}

.cal-date {
  width: 120px;
  display: inline-block;
  text-align: center;
  padding: 4px;
  font-size: 0.75em;
  vertical-align: middle;
}

.cal-match {
  width: calc(100% - 120px);
  display: inline-block;
  padding: 2px 4px;
}

.cal-match > table {
  width: 100%;
  margin-bottom: 0;
}

.cal-match > table > tbody > tr > td:nth-child(1),
.cal-match > table > tbody > tr > td:nth-child(3) {
  width: 42.5%;
}

.cal-match > table > tbody > tr > td:nth-child(2) {
  width: 15%;
  min-width: 70px;
  text-align: center;
}

.cal-logo {
  height: 25px;
}

.cal-team-left {
  padding: 2px;
  padding-left: 10px;
  text-align: left;
}

.cal-team-right {
  padding: 2px;
  padding-right: 10px;
  text-align: right;
}

@media only screen and (max-width: 690px) {
  .cal-date {
    width: 80px;
  }

  .cal-match {
    width: calc(100% - 80px);
  }
}

@media only screen and (max-width: 410px) {
  .cal-elem-row {
    display: block !important;
  }

  .cal-date {
    width: 100%;
    padding-bottom: 0px;
  }

  .cal-match {
    width: 100%;
    padding-bottom: 0px;
  }
}

.tbl-referees {
  margin: 0;
}

.tbl-referees > tbody > tr {
  border: 0;
}

.tbl-referees > tbody > tr > td {
  border: 0;
  padding: 3px 8px;
  text-align: center;
  vertical-align: middle;
}

h3.SquadraName {
  font-family: "Sora", sans-serif;
  margin-top: 10px;
}

.title-badge {
  display: inline-block;
  margin: 0 auto;
  font-family: "Sora", sans-serif;
  padding: 0.25em 0.75em;
  font-weight: 600;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25rem;
}

.sora {
  font-family: "Sora", sans-serif;
}

.table-match > tbody > tr > td.col1,
.table-match > tbody > tr > td.col2,
.table-match > tbody > tr > td.col3,
.table-match > tbody > tr > td.col5,
.table-match > tbody > tr > td.col6,
.table-match > tbody > tr > td.col7,
.table-match > thead > tr > th.col1,
.table-match > thead > tr > th.col2,
.table-match > thead > tr > th.col3,
.table-match > thead > tr > th.col5,
.table-match > thead > tr > th.col6,
.table-match > thead > tr > th.col7 {
  text-align: center;
  width: 7%;
  min-width: 34px;
}

.table-match-striped {
  border-bottom: 1px solid #ccc;
  margin-bottom: 0;
}

.table-match > tbody > tr,
.table-match > thead > tr {
  padding: 0;
}

.table-match > tbody > tr > td,
.table-match > thead > tr > th {
  padding: 2px 3px;
  vertical-align: middle;
}

/* Actions element */
.action-elem {
  padding: 0.35rem 0.6rem;
  margin-bottom: 4px;
  border: 1px solid #eee;
  border-right-width: 0.25rem;
  border-radius: 0.4rem;
  position: relative;
  font-weight: 400;
}

.action-elem > p {
  margin: 0;
  font-size: 90%;
}

.action-elem > h5 {
  margin: 0;
  margin-bottom: 2px;
  font-size: 1.15em;
  font-weight: 600;
}

.action-elem > span.badge {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-size: 80%;
}

.action-elem > img {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  height: 35px;
  border-radius: 0.25rem;
  width: auto;
}

.actions-div {
  max-height: 95vh;
  overflow: auto;
  scrollbar-width: 5px;
}

/* horizontal timeline */
table.timeline-oriz {
  width: 100%;
  border-collapse: collapse;
  margin: 0;
  position: relative;
}

table.timeline-oriz > tbody > tr > td {
  position: relative;
}

table.timeline-oriz > tbody > tr > td.head {
  width: 70px;
  text-align: left;
  vertical-align: middle;
}

/* vertical timeline */
table.timeline-vert {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  margin: 0;
  table-layout: fixed;
  display: table;
}

table.timeline-vert > tbody > tr > td {
  position: relative;
}

table.timeline-vert > tbody > tr.head {
  height: 60px;
}

table.timeline-vert > tbody > tr.head > td {
  text-align: center;
}

table.timeline-vert > tbody > tr.head > td:nth-child(1),
table.timeline-vert > tbody > tr.head > td:nth-child(3) {
  width: calc(50% - 20px);
}

/* separator */
.tl-oriz-sep {
  width: 100%;
  position: relative;
}

.tl-oriz-sep > .tl-label {
  position: absolute;
  top: 0;
  margin-top: -10px;
  margin-left: -10px;
}

.tl-vert-sep {
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
}

.tl-vert-sep > .tl-line {
  position: absolute;
  width: 1px;
  border: 0;
  border-left: 1px solid #ddd;
  height: 100%;
  left: calc(50% - 1px);
  top: 0;
}

.tl-vert-sep > .tl-label {
  position: absolute;
  width: 100%;
  left: 0;
  margin-top: -10px;
}

/* containers */

.tl-elem {
  position: absolute;
}

.tl-oriz-cont {
  width: 100%;
  height: 85px;
  position: relative;
  margin-right: 30px;
}

.tl-oriz-cont.tl-top > .tl-elem { transform: translate(-50%, 0); }
.tl-oriz-cont.tl-top > .tl-elem.tl-row1 { bottom: 4px; }
.tl-oriz-cont.tl-top > .tl-elem.tl-row2 { bottom: 26px; }
.tl-oriz-cont.tl-top > .tl-elem.tl-row3 { bottom: 48px; }
.tl-oriz-cont.tl-top > .tl-elem.tl-row4 { bottom: 70px; }
.tl-oriz-cont.tl-top > .tl-elem.tl-row5 { bottom: 70px; }

.tl-oriz-cont.tl-bottom > .tl-elem { transform: translate(-50%, 0); }
.tl-oriz-cont.tl-bottom > .tl-elem.tl-row1 { top: 4px; }
.tl-oriz-cont.tl-bottom > .tl-elem.tl-row2 { top: 26px; }
.tl-oriz-cont.tl-bottom > .tl-elem.tl-row3 { top: 48px; }
.tl-oriz-cont.tl-bottom > .tl-elem.tl-row4 { top: 70px; }
.tl-oriz-cont.tl-bottom > .tl-elem.tl-row5 { top: 70px; }

.tl-vert-cont {
  width: 100%;
  height: 100%;
  position: relative;
  margin-bottom: 40px;
}

.tl-vert-cont.tl-left > .tl-elem { transform: translate(+50%, 0); }
.tl-vert-cont.tl-left > .tl-elem.tl-row1 { right: 8%; }
.tl-vert-cont.tl-left > .tl-elem.tl-row2 { right: 28.5%; }
.tl-vert-cont.tl-left > .tl-elem.tl-row3 { right: 49%; }
.tl-vert-cont.tl-left > .tl-elem.tl-row4 { right: 69.5%; }
.tl-vert-cont.tl-left > .tl-elem.tl-row5 { right: 90%; }

.tl-vert-cont.tl-right > .tl-elem { transform: translate(-50%, 0); }
.tl-vert-cont.tl-right > .tl-elem.tl-row1 { left: 8%; }
.tl-vert-cont.tl-right > .tl-elem.tl-row2 { left: 28.5%; }
.tl-vert-cont.tl-right > .tl-elem.tl-row3 { left: 49%; }
.tl-vert-cont.tl-right > .tl-elem.tl-row4 { left: 69.5%; }
.tl-vert-cont.tl-right > .tl-elem.tl-row5 { left: 90%; }

/* elements */

.tl-elem > span.fa {
  font-size: 0.8em;
}

.tl-elem > span.badge {
  border: 0.5px solid #ccc;
}

.table-squadra > tbody > tr,
.table-squadra > thead > tr {
  padding: 0;
}

.table-squadra > tbody > tr > td,
.table-squadra > thead > tr > th {
  padding: 2px 3px;
  vertical-align: middle;
}

.table-squadra > tbody > tr > td.col1,
.table-squadra > thead > tr > th.col1 {
  width: 28px;
  text-align: center;
}

.table-squadra > tbody > tr > td.col3,
.table-squadra > tbody > tr > td.col4,
.table-squadra > tbody > tr > td.col5,
.table-squadra > tbody > tr > td.col6,
.table-squadra > tbody > tr > td.col7,
.table-squadra > thead > tr > th.col3,
.table-squadra > thead > tr > th.col4,
.table-squadra > thead > tr > th.col5,
.table-squadra > thead > tr > th.col6,
.table-squadra > thead > tr > th.col7 {
  text-align: center;
  width: 7%;
  min-width: 34px;
}

.table-striped {
  border-bottom: 1px solid #ccc;
  margin-bottom: 0;
}

.nv {
  color: #aaa;
}

